import React from 'react';
import LabCard from '../cards/Campus/LabCard';
import img1 from "../img/dot1.jpg";
import img2 from "../img/dot2.jpg";

const Dot = () => {
  let demoDot = [
    {
      "name": "Mrs. M. Jyothi rani.M.A.TELUGU,B.Ed  ,UGC-NET,AP-SET.",
      "img": img1,
      "id": 1
    }, {
      "name": "Dr.K.Lavanya I.M.A Telugu, UGC-JRF, NET, TS-SET,  Ph.D (Telugu)",
      "img": img2,
      "id": 2
    },
  ];

  return (
    <>
      <div className="px-4 py-16 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-20">
        <div className="max-w-xl mb-10 md:mx-auto sm:text-center lg:max-w-2xl md:mb-12">
          <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold leading-none tracking-tight text-gray-900 sm:text-4xl md:mx-auto" data-aos="fade-up">
            <span className="relative inline-block">
              <svg viewBox="0 0 52 24" fill="currentColor" className="absolute top-0 left-0 z-0 hidden w-32 -mt-8 -ml-20 text-blue-gray-100 lg:w-32 lg:-ml-28 lg:-mt-10 sm:block">
                <defs>
                  <pattern id="1d4040f3-9f3e-4ac7-b117-7d4009658ced" x="0" y="0" width=".135" height=".30">
                    <circle cx="1" cy="1" r=".7" />
                  </pattern>
                </defs>
                <rect fill="url(#1d4040f3-9f3e-4ac7-b117-7d4009658ced)" width="52" height="24" />
              </svg>
              <h2 class="text-3xl p-4">
  <span className="relative text-3xl text-blue-700 ">Department Of Telugu</span>
</h2>
            </span>{' '}
          </h2>
          <ul class="text-base text-gray-700 md:text-lg" data-aos="fade-up">
  <li className='text-3xl p-4'>About the department:</li>
  <li>
    Telugu is a primary language spoken in the state of Telangana. The Department of Telugu has been providing language learning skills for the students. This department is being run with two faculty members.
  </li>
  <br />
  <li>Objectives:</li>
  <li>● It inculcates Social Values and morals in the students</li>
  <li>● It enables the students to develop interest towards this language</li>
  <li>● It spread the greatness of Telugu Language, Culture, Art and Tradition as state festivals</li>
  <li>● It brought out the beauty in the Spoken Language</li>
</ul>
<br />
<ul class="text-base text-gray-700 md:text-lg" data-aos="fade-up">
  <li className='text-3xl'>Programmes Offered:</li>
  <li>● B.A (HEP )</li>
  <li>● B.A (HPE)</li>
  <li>● B.Com (General)</li>
  <li>● B.Com (Computer)</li>
  <li>● B.Com (Business Analytics) </li>
  <li>● B.Sc (BZC)</li>
  <li>● B.Sc (BBC)</li>
  <li>● B.Sc (MPC)</li>
  <li>● B.Sc (MPCs)</li>
  <li>● B.Sc (MSCs)</li>
  <li>● B.Sc ( Data Science)</li>
</ul>
<br />
<ul class="text-base text-gray-700 md:text-lg" data-aos="fade-up">
  <li>Courses Offered:</li>
  <li>● Sahithi Manjeera</li>
  <li>● Sahithi Kinnera</li>
</ul>
<br />
<p class="text-3xl text-gray-700 md:text-lg" data-aos="fade-up">
  Faculty:
</p>
<br />
<span className="relative text-3xl text-blue-700 p-10">Mrs. M. Jyothi rani</span>
<p class="m-10 text-base text-gray-700 md:text-lg" data-aos="fade-up">
  M.JYOTHI RANI is a faculty member in the Department of Telugu. She holds a Master's degree in Telugu from Kakatiya University, Qualified AP-SET and UGC-NET in the year 2013 and also B.Ed from Osmania University. She had a teaching experience of 10 years.She had 5 years experience as a teacher in secondary school at Vedhasri Naveena Patashala Tharallapally, warangal. Later worked as a Guest faculty at Mahatma Jyothiba Pule BC welfare Residential Jr. College, L. M. D colony Karimnagar for 1 year and contract lecturer at KGBV, Station Ganpur for 1 year and then as Guest faculty at Telangna Tribal welfare Residential Degree college, Janagoan for 3 years, before she joined this institution. She joined this institution on 13-03-2019.
</p>
<br />
<span className="relative text-3xl text-blue-700 ">Dr.K.Lavanya </span>
<p class="m-10 text-base text-gray-700 md:text-lg" data-aos="fade-up">
  Dr. K. Lavanya is a Degree Lecturer in Telugu. She Qualified UGC-NET in 2015. She got UGC-JRF and NET in 2017. She Qualified TS-SET in 2017. She has Completed Ph.D from University of Hyderabad in 2019. She has 3 years of Research Experience. She Published 3 research papers. She has Two years of Experience in Teaching. She has attended various National Seminars.
</p>
        </div>
        <div className='flex flex-col justify-evenly '>
          <div className="flex flex-wrap justify-around items-center p-2">
            {demoDot.map((Dot) => (
              <div key={Dot.id} className="mb-4 w-full flex justify-center md:w-1/3 px-2">
                <LabCard name={Dot.name} img={Dot.img} />
              </div>
            ))}
          </div>
        </div>
      </div>
    </>
  )
}

export default Dot;
