import React from 'react'
import LabCard from '../cards/Campus/LabCard'
import img1 from "../img/bot1.png"
import img2 from "../img/bot2.png"

const Botany = () => {

  let demoBotany = [
    {
      "name": "Dr. B. SONI VEENA",
      "img": img1,
      "id": 1
    }, {
      "name": "Mrs. CH. RATNAKALA",
      "img": img2,
      "id": 2
    },
  ]

  return (
    <>
      <div className="px-4 py-16 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-20">
      <div className="max-w-xl mb-10 md:mx-auto sm:text-center lg:max-w-2xl md:mb-12">
        <h2
          className="max-w-lg mb-6 font-sans text-3xl font-bold leading-none tracking-tight text-gray-900 sm:text-4xl md:mx-auto"
          data-aos="fade-up"
        >
          <span className="relative inline-block">
            <svg
              viewBox="0 0 52 24"
              fill="currentColor"
              className="absolute top-0 left-0 z-0 hidden w-32 -mt-8 -ml-20 text-blue-gray-100 lg:w-32 lg:-ml-28 lg:-mt-10 sm:block"
            >
              <defs>
                <pattern
                  id="1d4040f3-9f3e-4ac7-b117-7d4009658ced"
                  x="0"
                  y="0"
                  width=".135"
                  height=".30"
                >
                  <circle cx="1" cy="1" r=".7" />
                </pattern>
              </defs>
              <rect
                fill="url(#1d4040f3-9f3e-4ac7-b117-7d4009658ced)"
                width="52"
                height="24"
              />
            </svg>
           <span className="relative text-3xl text-blue-700 ">Department Of Botany</span>
          </span>{' '}
        </h2>
        <p className="text-base text-gray-700 md:text-lg" data-aos="fade-up">
        </p>
        <ul className="text-base text-gray-700 md:text-lg" data-aos="fade-up">
  <li className='text-3xl p-4'>About the department:</li>
          <li>
          The department is led by well qualified and committed faculty. The team of this department are always engaged in developing the optimal teaching and learning technique to deal with and inspire the students, during a healthy and friendly atmosphere, consistent with their learning capacity. The primary and foremost precedence of the Department of Botany is the overall development of each and every student of the college.
          </li>
          <br></br>
          <li>Objectives:</li>
          <li>• To achieve academic excellence in basic and applied aspects of plant sciences.</li>
          <li>• To understand the impact of plant diversity in the environment.</li>
          <li>• To inculcate scientific awareness towards protecting diversity and Eco-environmental management of plant resources for sustainable development.</li>
          <li>• To gain knowledge of plant breeding techniques.</li>
          <li>• To motivate the students to undertake PG studies in plant sciences</li>
        </ul>
        <br></br>
        <ul className="text-base text-gray-700 md:text-lg" data-aos="fade-up">
          <li className='text-3xl'>Programmes Offered:</li>
          <li>●	BSc. Bachelor of Science (B.Z.C) </li>
        </ul>
        <br></br>
<p class="text-3xl text-gray-700 md:text-lg" data-aos="fade-up">
  Faculty:
</p>
        <br></br>
       <span className="relative text-3xl text-blue-700 ">Dr. B. SONI VEENA</span>
        <p className="m-10 text-base text-gray-700 md:text-lg" data-aos="fade-up">
        Dr. B, Soni Veena is a Degree Lecturer in Botany, Department of Botany, MJPTBCW Residential Degree College for Women-Wargal. She has received B.Sc., B.Ed. and M.Sc. from Osmania University, Hyderabad. She had qualified for the SET in 2014 for Botany and pursued Ph.D. from the Department of Botany, Osmania University for the topic “Isolation and phytochemical analysis of bio-active compounds from some endemic plants”. Till now she has published about 8 research papers in reputed National and International journals. She has attended four National Seminars and Conferences.  She has overall experience of teaching in Botany for 4 years at the undergraduate level.
    	Dr. B. S. Veena organized One day Workshop on “Plant Biotechnology in Human Welfare” and plantation of herbal garden at MJPTBC Residential Degree College for Women-Wargal. She served as Deputy Warden for the academic year 2020-21 at MJPTBC Residential Degree College for Women-Wargal.

        </p>
        <br></br>
       <span className="relative text-3xl text-blue-700 ">Mrs. CH. RATNAKALA</span>
        <p className="m-10 text-base text-gray-700 md:text-lg" data-aos="fade-up">
        Mrs. CH. Ratnakala is a faculty member in the Department of Botany. She Holds Master's degree in Botany and Bachelor of Education from Osmania University. She has five years of teaching experience. She has attended National seminars. 
After completing graduation, student can go for higher courses like M.Sc. (Botany), M.Sc.(Bio-technology), M.Sc.(Bio-Chemistry), M.Sc.(Micro Biology) and M.Sc.(Genetics). Amongst the careers available to a person who enjoys outdoors are positions as an ecologist, taxonomist, conservationist, and forester. Person with mathematical background might find biophysics, genetics, and developmental biology to be exciting fields. Someone with an interest in chemistry might become a plant physiologist, plant biochemist, molecular biologist or chemo taxonomist. Student can also choose biotechnology, microbiology, mycology and phycology. Student can also option for ornamental horticulture and landscape design. Student can obtain specialization in plant breeding, plant pathology, agronomy and horticulture. Teaching botany is challenging and rewarding career. Some botanist work in marketing or administration of plant related industries such as pharmaceutical companies, seed companies, biotechnology firms, scientific publishers and biological supply houses. Other plant biologists work in museums, herbaria and botanical gardens. Some with additional training become scientific writers, computer programmers and botanical illustrators. 

        </p>

      

      </div>
        <div className='flex flex-col justify-evenly '>
          <div className="flex flex-wrap justify-around items-center p-2">
            {demoBotany.map((Botany) => (
              <div key={Botany.id} className="mb-4 w-full flex justify-center md:w-1/3 px-2">
                <LabCard name={Botany.name} img={Botany.img} />
              </div>
            ))}
          </div>
        </div>
      </div>
    </>
  )
}

export default Botany
