import React from 'react'

import LabCard from '../cards/Campus/LabCard'

import img1 from "../img/cse1.jpg"
import img2 from "../img/cse2.jpg"

const Cs = () => {

  let demoCs = [
    {
      "name": "Dr.S.Radharani",
      "img": img1,
      "id": 1
    }, {
      "name":"Dr. K. Nikitha",
      "img": img2,
      "id": 2
    },
  ]

  return (
    <>
      <div className="px-4 py-16 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-20">
      <div className="max-w-xl mb-10 md:mx-auto sm:text-center lg:max-w-2xl md:mb-12">
        <h2
          className="max-w-lg mb-6 font-sans text-3xl font-bold leading-none tracking-tight text-gray-900 sm:text-4xl md:mx-auto"
          data-aos="fade-up"
        >
          <span className="relative inline-block">
            <svg
              viewBox="0 0 52 24"
              fill="currentColor"
              className="absolute top-0 left-0 z-0 hidden w-32 -mt-8 -ml-20 text-blue-gray-100 lg:w-32 lg:-ml-28 lg:-mt-10 sm:block"
            >
              <defs>
                <pattern
                  id="1d4040f3-9f3e-4ac7-b117-7d4009658ced"
                  x="0"
                  y="0"
                  width=".135"
                  height=".30"
                >
                  <circle cx="1" cy="1" r=".7" />
                </pattern>
              </defs>
              <rect
                fill="url(#1d4040f3-9f3e-4ac7-b117-7d4009658ced)"
                width="52"
                height="24"
              />
            </svg>
           <span className="relative text-3xl text-blue-700 ">Department Of Computer Science</span>
          </span>{' '}
        </h2>
        <p className="text-base text-gray-700 md:text-lg" data-aos="fade-up">
        </p>
        <ul className="text-base text-gray-700 md:text-lg" data-aos="fade-up">
  <li className='text-3xl p-4'>About the department:</li>
          <li>The Department of Computer Science is supported by dedicated, well qualified and experienced faculty. Department has a well established laboratory with 83 computers.  The Department organizes various activities for enhancing students' computer skills. Students were encouraged in getting placement in reputed IT companies. Students interested in PG are getting admission in top Universities. Students are motivated to participate and organize extra-curricular activities.  
          </li>
          <br></br>
          <li>Objectives:</li>
          <li>●	pursue advanced education and other creative works in technology</li>
          <li>●	understand the fundamental principles of Computer Science</li>
          <li>●	Graduates will be successfully working on computing profession</li>
        </ul>
        <br></br>
        <ul className="text-base text-gray-700 md:text-lg" data-aos="fade-up">
          <li className='text-3xl'>Programmes Offered:</li>
          <li>●	B.COM(CA)</li>
          <li>●	B.COM (BA)</li>
          <li>●	BSc.MPCs</li>
          <li>●	BSc.MSCs</li>
          <li>●	BSc.MSDS </li>
        </ul>
        <br></br>
        <br></br>
<p class="text-3xl text-gray-700 md:text-lg" data-aos="fade-up">
  Faculty:
</p>
        <br></br>
       <span className="relative text-3xl text-blue-700 ">Dr.S.Radharani</span>
        <p className="m-10 text-base text-gray-700 md:text-lg" data-aos="fade-up">
        M.C.A(Master of Computer Applications from OU, 1997), M. Tech.( 2012), Ph. D. in the field of security in cloud computing.
Worked as Assistant Professor in  Department of CSE,University College of Engineering, Osmania University Hyderabad (2098-2019) 21 years. 24 years of teaching and research experiences. Qualified  in APSET. Published about 5 papers in peer reviewed International Journals indexed in SCI, Scopus and  in  International Conferences.  Organized various conferences and workshops including an International Conferences sponsored by IEEE. Life Member of professional bodies  IETE. Working in the field of Security issues in Cloud Computing .
        </p>
        <br></br>
       <span className="relative text-3xl text-blue-700 ">Dr. K. Nikitha</span>
        <p className="m-10 text-base text-gray-700 md:text-lg" data-aos="fade-up">
        B.Tech (Electronics and Communications Engg., JNTUH, 2003),
M. Tech.(Information Technology, Andhra University, 2008),
Ph. D.(Engg., GITAM University, Visakhapatnam, 2015) in the field of Wireless Sensor Networks.
Worked as Associate Professor in Information Technology  Department, MVSR Engineering College, Hyderabad (2017-19) two years. Formerly, as Assistant Professor in the Department of Computer Science and Engineering, GITAM University (2005-16). 16 years of teaching and research experiences. Qualified UGC-NET and TSET.
Published about 15 papers in peer reviewed International Journals indexed in SCI, Scopus and about 10 in National and International Conferences. Organized various conferences and workshops including an International Conference on Wireless and Optical Communications Networks sponsored by IEEE. Life Member of professional bodies CSI, ISTE, IAENG and Indian National Congress. Working in the field of Wireless and Sensor Networks, Optimization Techniques

        </p>

      

      </div>
        <div className='flex flex-col justify-evenly '>
          <div className="flex flex-wrap justify-around items-center p-2">
            {demoCs.map((Cs) => (
              <div key={Cs.id} className="mb-4 w-full flex justify-center md:w-1/3 px-2">
                <LabCard name={Cs.name} img={Cs.img} />
              </div>
            ))}
          </div>
        </div>
      </div>
    </>
  )
}

export default Cs